    // border-top: 1px solid var(--divider-color);

    .label {
      color: var(--icon-color);
    }

    .icon {
      color: var(--icon-color-inverted);
      // color: var(--body-text-color);
    }

    .description {
      @extend .text-sm;
      color: var(--icon-color-inverted);
      margin-top: 4px;
    }

    &:hover {
      cursor: pointer;

      .icon,
      .label {
        color: var(--secondary);
      }
